import React from "react";
import {OraMessage, OraInput} from "eshop/components/OraCommonComponents";
import {addLabelToInput, appendErrors, appendErrorsNotTimeouted, maskField, mapDisplayValue, addLabelToInputPesel, addLabelToInputPhone, addLabelToInputIdNumber, addLabelToInputNip, addLabelToInputKna, addLabelToInputCensoredIdNumber} from "./hoc/input";
import {LabeledAutocomplete} from "./ui/Autocomplete";

//TODO: Component refactored to not duplicate styleguide mask module and handle blur event. Check LabeledInput.

export var InputWithPostalCodeMask = maskField(
    function map(value){
        return /^(\d{0,5}-?\d{0,5})$/.test(value) && value.replace("-", "").length<=5;
    })(OraInput);
export var InputWithPostalCodeMaskMobile = maskField(value =>/^(\d{0,5}|)$/.test(value))(OraInput);


export var OraPostalCodeInput = mapDisplayValue({
        mapToDisplay: value => /^\d{3,5}$/.test(value.replace("-", "")) ? value.replace("-", "").slice(0,2) + "-" + value.replace("-", "").slice(2) : value,
        mapToValue: display => /^(\d{0,2})-?(\d{0,3})$/.test(display) ? display.replace("-", "") : display
    })(InputWithPostalCodeMask);

export var OraPostalCodeInputMobile = mapDisplayValue({
    mapToDisplay: value =>  value,
    mapToValue: display =>  display
})(InputWithPostalCodeMaskMobile);

export var InputWithPhoneNumberMask = maskField(value => /^(\d{1})|([\d\(]{1}\+48\) (\d{0,3}-?|\d{0,3})(\d{0,3}-?|\d{0,3})\d{0,3})$/.test(value))(OraInput);
export var InputWithPhoneNumberMobileMask = maskField(value => /^(\d{0,9}|)$/.test(value))(OraInput);
export var OraPhoneNumberInput = mapDisplayValue({
        mapToDisplay: value => mapMsisdn(value),
        mapToValue: display => replace(display)
    })(InputWithPhoneNumberMask);

export var OraPhoneNumberInputMobile = mapDisplayValue({
    mapToDisplay: value => (value),
    mapToValue: display => (display)
})(InputWithPhoneNumberMobileMask);

export var InputWithNipMask = maskField(value => /^((\d{3}-?|\d{0,3})(\d{3}-?|\d{0,3})(\d{2}-?|\d{0,2})(\d{0,2}))$/.test(value))(OraInput);
export var InputWithNipMobileMask = maskField(value => /^(\d{0,10}|)$/.test(value))(OraInput);
export var OraNipMobileInput = mapDisplayValue({
    mapToDisplay: value => value,
    mapToValue: display => display
})(InputWithNipMobileMask);

export var OraNipInput = mapDisplayValue({
    mapToDisplay: value => mapNip(value),
    mapToValue: display => !display ? "" : display.replace(/-/g , "")
})(InputWithNipMask);

function mapNip(value){
    if(!value)
        return "";
    if (/^.{8,10}$/.test(value))
        return value.slice(0,3) + "-" + value.slice(3,6) + "-" + value.slice(6,8) + "-" + value.slice(8);
    else if (/^.{6,8}$/.test(value))
        return value.slice(0,3) + "-" + value.slice(3,6) + "-" + value.slice(6);
    else if (/^.{3,6}$/.test(value))
        return value.slice(0,3) + "-" + value.slice(3);
    else
        return value;
}

export var InputWithRegonMask = maskField(value => /^\d{0,14}$/.test(value))(OraInput);

export var OraRegonInput = mapDisplayValue({
    mapToDisplay: value => !value ? "" : value,
    mapToValue: display => !display ? "" : display
})(InputWithRegonMask);

function mapMsisdn(value) {
    if (/^.{6,9}$/.test(value))
        return "(+48) " + value.slice(0,3) + "-" + value.slice(3,6) + "-" + value.slice(6);
    else if (/^.{3,6}$/.test(value))
        return "(+48) " + value.slice(0,3) + "-" + value.slice(3);
    else if (/^.{1,3}$/.test(value))
        return "(+48) " + value;
    else
        return value;
}

function replace(display){
    return /^(.{2,17})$/.test(display) ? checkIfCopied(display) : display;
}

function checkIfCopied(display){
    return /^([\d\(]{1}\+48\) (\d{3}-?|\d{0,3})(\d{3}-?|\d{0,3})\d{0,3})$/.test(display) ? display.replace(/\D/g, "").slice(2, display.replace(/\D/g, "").length) : display.replace(/\D/g, "");
}

function mapKna(value) {
    if (/^.{7,9}$/.test(value))
        return value.slice(0,2) + "-" + value.slice(2,5) + "-" + value.slice(5, 7) + "-" + value.slice(7);
    else if (/^.{5,7}$/.test(value))
        return value.slice(0,2) + "-" + value.slice(2,5) + "-" + value.slice(5);
    else if (/^.{2,5}$/.test(value))
        return value.slice(0,2) + "-" + value.slice(2);
    else if (/^.{1,2}$/.test(value))
        return value;
    else
        return value;
}


export var InputWithKnaNumberMask = maskField(value => {
    value = value.replace(/_/g, "").replace(/-/g, "");
    return  /^(\d{0,2}|\d{2}-?\d{0,3}-?\d{0,2}-?\d{0,2})$/.test(value);
    })(OraInput);

export var OraKnaInput = mapDisplayValue({
                mapToDisplay: value => mapKna(value),
                mapToValue: display => display ? display.replace(/_/g, "").replace(/-/g, "") : display
            })(InputWithKnaNumberMask);


