export var OraLabeledPhoneMobile = addLabelToInputPhone(OraPhoneNumberInputMobile);
export var OraLabeledIdNumber = addLabelToInputIdNumber(OraInput);
export var OraLabeledCensoredIdNumber = addLabelToInputCensoredIdNumber(OraInput);
export var OraLabeledKna = addLabelToInputKna(OraKnaInput);
export var OraLabeledNip = addLabelToInputNip(OraNipInput);
export var OraLabeledNipMobile = addLabelToInputNip(OraNipMobileInput);
export var OraLabeledRegon = addLabelToInput(OraRegonInput);

// Component mapping a validation error object (see eshop/modules/core/validation) to a message component
export const ErrorMessage = ({level, message}) => <OraMessage type={level} text={message}/>;
export const withErrorMessage = appendErrors(ErrorMessage);
export const withErrorMessageNotTimeouted = appendErrorsNotTimeouted(ErrorMessage);


export const LabeledInputWithErrors = withErrorMessage(OraLabeledInput);
export const LabeledPostalCodeInputWithErrors = withErrorMessage(OraLabeledPostalCodeInput);
export const LabeledPostalCodeInputMobileWithErrors = withErrorMessage(OraLabeledPostalCodeInputMobile);
export const LabeledCbsInputWithErrors = withErrorMessage(OraLabeledPostalCodeInput);
export const LabeledAutocompleteWithErrors = withErrorMessage(LabeledAutocomplete);

export const LabeledPeselWithErrors = withErrorMessage(OraLabeledPesel);
export const LabeledIdNumberWithErrors = withErrorMessage(OraLabeledIdNumber);
export const LabeledIdCensoredNumberWithErrors = withErrorMessage(OraLabeledCensoredIdNumber);
export const LabeledPhoneNumberInputWithErrors = withErrorMessage(OraLabeledPhone);
export const LabeledPhoneNumberInputMobileWithErrors = withErrorMessage(OraLabeledPhoneMobile);
export const LabeledNipInputWithErrors = withErrorMessage(OraLabeledNip);
export const LabeledNipMobileInputWithErrors = withErrorMessage(OraLabeledNipMobile);
export const LabeledRegonInputWithErrors = withErrorMessage(OraLabeledRegon);
export const LabeledKnaNumberInputWithErrors = withErrorMessageNotTimeouted(OraLabeledKna);